<template>
	<view class="container" v-if="loading">
		<uni-notice-bar single="true" text="温馨提示：如发现工程师中途无故加价，请拒绝付款！"></uni-notice-bar>
		<view class="box">
			<view class="box-t">
				<view class="title">客户信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cell-box">
					<view class="cell-box-item">
						<view class="left">预约时间</view>
						<view class="right">{{model.yuyuedate}} {{model.yuyuetime}}</view>
					</view>
					<view class="cell-box-item">
						<view class="left">服务地址</view>
						<view class="right">
							{{model.receiver_address}}
						</view>
					</view>
					<view class="cell-box-item">
						<view class="left">姓名</view>
						<view class="right">
							{{model.receiver_name}}
						</view>
					</view>
					<view class="cell-box-item">
						<view class="left">电话</view>
						<view class="right">{{model.receiver_mobile}} <text style="padding: 0 15rpx;">|</text> <text
								class="blue" @click="makecall(model.receiver_mobile)">拨打</text></view>
					</view>
				</view>
			</view>
		</view>

		<view class="box">
			<view class="box-t">
				<view class="title">服务信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cart-lists">
					<view class="item">
						<image :src="model.service_thumb" mode="scaleToFill" class="thumb">
						</image>
						<view class="info">
							<view class="title">
								{{model.service_name}}
								<text>x1</text>
							</view>
							<view class="num">￥{{model.price}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="box" v-if="model.status==3">
			<view class="box-t">
				<view class="title">维修记录</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cell-box">
					<view class="cell-box-item">
						<view class="left">保修到期</view>
						<view class="right">{{model.record.baoxiu}}</view>
					</view>
					<view class="cell-box-item">
						<view class="left">微信金额</view>
						<view class="right">
							{{model.record.price}}元
						</view>
					</view>
					<view class="cell-box-item">
						<view class="pictures">
							<view class="item" v-for="(item,index) in model.record.pictures" @click="preview(i)">
								<image :src="item.url"></image>
							</view>
						</view>
					</view>
					<view class="cell-box-item">
						<view class="left">备注</view>
						<view class="right">
							{{model.record.content}}
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="box">
			<view class="box-t">
				<view class="title">订单信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cell-box">
					<view class="cell-box-item">
						<view class="left">订单号码</view>
						<view class="right">{{model.orderid}} <text style="padding: 0 15rpx;">|</text> <text
								class="blue" @click="copy(model.orderid)">复制</text></view>
					</view>
					<view class="cell-box-item">
						<view class="left">下单时间</view>
						<view class="right">
							{{model.created_at}}
						</view>
					</view>
				</view>

			</view>
		</view>

		<view class="box" v-if="model.status==1">
			<view class="box-t">
				<view class="title">操作订单</view>
				<view class="more"></view>
			</view>
			<view class="box-btns">				
				<view class="btn plain" @click="cancelOrder">取消订单</view>
				<view class="btn" @click="queren">确认接单</view> 
			</view>		 
		</view>
		<view class="box" v-if="model.status==2">
			<view class="box-t">
				<view class="title">操作订单</view>
				<view class="more"></view>
			</view>
			<view class="box-btns">				
				<view class="btn plain" @click="cancelOrder">取消订单</view>
				<view class="btn" @click="$refs.comment.open()">订单核价</view> 
			</view>		 
		</view>
		<view style="height: 20rpx;"></view>
		<uni-popup ref="comment">
			<view class="comment-box">
				<view class="title">维修说明</view>
				<uni-datetime-picker :border="false" type="date" v-model="comment.baoxiu">
					<view class="textarea">
						<view class="label">保修到期：</view>
						<uni-easyinput v-model="comment.baoxiu" placeholder="请选择保修期" type="text" disabled
							:inputBorder="false" class="input"></uni-easyinput>
					</view>
				</uni-datetime-picker>
				<view class="textarea">
					<view class="label">总金额：</view>
					<uni-easyinput v-model="comment.price" placeholder="请输入维修金额" type="text" :inputBorder="false" class="input">
					</uni-easyinput>
				</view>
				<view class="textarea">
					<view class="label" style="align-items: flex-start;">维修概况：</view>
					<uni-easyinput v-model="comment.content"  type="textarea" :inputBorder="false" class="input"></uni-easyinput>
				</view>
				<view class="upload">
					<uni-file-picker v-model="comment.thumbs" fileMediatype="image" mode="grid" file-extname="png,jpg"
						:limit="9" @select="select" />
				</view>
				<view class="btn" @click="saveComment">确定维修</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				loading:false,
				id: 0,
				model: {},
				address: {},
				comment: {
					order_id: 0,
					teacher_id: 0,
					content: '',
					thumbs: [],
					baoxiu: ''
				}
			}
		},
		onLoad(option) {
			this.id = option.id
			this.comment.order_id = this.id
			this.getContent()
		},
		methods: {
			async getContent() {
				let res = await this.$u.get('/v2/shop/order/show', {
					id: this.id
				})
				this.model = res.data.model
				this.address = res.data.address
				this.loading = true
				console.log(res.data)
			},
			copy(orderid) {
				uni.setClipboardData({
					data: orderid, //要被复制的内容
					success: () => { //复制成功的回调函数
						uni.showToast({ //提示
							title: '复制成功'
						})
					}
				});
			},
			async queren(){
				uni.showModal({
					title: '提示',
					content: '确定要接受这个订单吗？',
					success: (res) => {
						if (res.confirm) {
							//取消订单
							this.$u.get('/v2/shop/order/enter', {
								id: this.id
							}).then((res) => {
								if (res.code == 200) {
									this.$u.toast('接受成功')
									uni.navigateBack({
										delta: 1
									})
								}
							})
						} else if (res.cancel) {
				
						}
					}
				});
			},
			async cancelOrder() {
				uni.showModal({
					title: '提示',
					content: '确定要取消这个订单吗？',
					success: (res) => {
						if (res.confirm) {
							//取消订单
							this.$u.get('/v2/shop/order/cancel', {
								uid: this.vuex_user.id,
								id: this.id
							}).then((res) => {
								if (res.code == 200) {
									this.$u.toast('取消成功')
									uni.navigateBack({
										delta: 1
									})
								}
							})
						} else if (res.cancel) {

						}
					}
				});
			},
 
			select(e) {
				const tempFilePaths = e.tempFilePaths;
				tempFilePaths.forEach((item) => {
					this.upload(item)
				})
			},
			upload(file) {
				uni.uploadFile({
					url: this.$baseUrl() + '/v2/file/images',
					filePath: file,
					name: 'file',
					success: (res) => {
						let data = JSON.parse(res.data)
						this.comment.thumbs.push({
							"name": data.data.name,
							"extname": data.data.extension,
							"url": data.data.url
						})
					},
					complete: (res) => {
						console.log(res);
					}
				});
			},
			async saveComment() {
				this.comment.order_id = this.id
				this.comment.teacher_id = this.model.teacher_id
				if(this.comment.thumbs.length==0){
					this.$u.toast('维修图片不能为空')
					return
				}
				if(this.comment.baoxiu==''){
					this.$u.toast('保修日期不能为空')
					return
				}
				if(this.comment.price==''){
					this.$u.toast('金额不能为空')
					return
				}
				let res = await this.$u.post('/v2/shop/order/save', this.comment)
				if (res.code == 200) {
					this.$refs.comment.close()
					this.$u.toast('保存成功')					
				}
			},
			makecall(tel) {
				uni.makePhoneCall({
					phoneNumber: tel
				})
			},
			preview(index) {
				let  urls = this.model.record.pictures.map((item)=>{
					return item.url
				})
				uni.previewImage({
					current:index,
					urls: urls,
					longPressActions: {
						success: function(data) {
							console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
						},
						fail: function(err) {
							console.log(err.errMsg);
						}
					}
				});
			},

		}
	}
</script>
<style>
	page{
		background-color: #F5F5F5;
	}
</style>
<style scoped>
	.container {
		display: block;
		min-height: 100vh;
		position: relative;
	}

	.segmented {
		background-color: #fff;
		width: 100vw;
		padding: 20rpx 0;
		display: flex;
		justify-content: space-around;
		font-size: 28rpx;
		position: sticky;
		top: var(--window-top);
		z-index: 600;
	}

	.segmented .item.active {
		color: #FF4400;
		border-bottom: #FF4400 1px solid;
		padding-bottom: 10rpx;
	}

	.box {
		border-radius: 20rpx;
		box-shadow: 0 0 20rpx rgba(0, 0, 0, 0.1);
		margin: 20rpx 20rpx 0 20rpx;
		background-color: #fff;
	}

	.box .box-t {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx;
	}

	.box .box-t .title {
		font-size: 32rpx;
		font-weight: bold;
	}

	.box .box-t .title .text {
		color: #777
	}

	.box .box-t .more {
		font-size: 24rpx;
		color: #FF4400
	}

	.box .box-btns {
		padding: 20rpx;
		display: flex;
		justify-content: space-around;
	}

	.box .box-btns .btn {
		border-radius: 40rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		color: #fff;
		width: 180rpx;
		height: 64rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 28rpx;
	}

	.box .box-btns .btn.plain {
		background: #fff;
		color: #FF4400;
		border: #FF4400 1px solid;
	}

	.cell-box {
		margin: 0 20rpx;
	}

	.cell-box .cell-box-item {
		border-top: #F8F8F8 1px solid;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 72rpx;
		padding: 10rpx 0;
		font-size: 28rpx;
	}

	.cell-box .cell-box-item .left {
		color: #999999;
		width: 160rpx;
	}

	.cell-box .cell-box-item .right {
		flex: 1;
		text-align: right;
	}

	.cell-box .cell-box-item .right .blue {
		color: #FF4400
	}

	.cart-lists {
		padding: 20rpx;
	}

	.cart-lists .item {
		display: flex;
		background-color: #fff;
		border-radius: 20rpx;
		padding: 20rpx;
		margin-bottom: 20rpx;


	}

	.cart-lists .item:last-child {
		border-bottom: none
	}

	.cart-lists .item .thumb {
		width: 120rpx;
		height: 120rpx;
		border-radius: 8rpx;
		margin-right: 20rpx;
	}

	.cart-lists .item .info {
		flex: 1;
		margin-top: 0;
		display: flex;
		flex-direction: column;
	}

	.cart-lists .item .info .title {
		font-size: 32rpx;
		display: flex;
		justify-content: space-between;
		margin-bottom: 20rpx;
		font-weight: bold;
	}

	.cart-lists .item .info .title text {
		font-size: 24rpx;
		color: #999;
		font-weight: normal;
	}

	.cart-lists .item .info .num {
		color: #F74437;
	}

	.comment-box {
		width: 80vw;
		background-color: #fff;
		border-radius: 20rpx;
		display: flex;
		flex-direction: column;
		padding: 20rpx;
	}

	.comment-box .title {
		text-align: center;
		font-size: 32rpx;
		font-weight: bold;
		margin: 20rpx 0;
	}

	.comment-box .star {
		margin: 10rpx 0;
	}

	.comment-box .textarea {
		margin: 10rpx 0;
		border: #E5E5E5 1px solid;
		border-radius: 8rpx;
		display: flex;
		align-items: center;
		background-color: #EEEEEE;

	}	
	.comment-box .textarea .label{
		font-size: 24rpx;
		color: #666;
		min-width: 140rpx;
		text-align: center;
	}
	.comment-box .textarea .input{
		flex:1;
	}
	.comment-box .upload {
		margin: 10rpx 0;
	}

	.comment-box .btn {
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		height: 90rpx;
		border-radius: 16rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFF;
		margin-top: 10rpx;
	}
	.pictures {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		justify-content: space-between;
		flex: 1;
	}
	
	.pictures .item image {
		width: 200rpx;
		height: 200rpx;
	}
</style>
